import { defineComponent, reactive, ref } from 'vue';
export default defineComponent({
  setup() {
    const phone = reactive({
      phonenum: '',
      verifycode: ''
    });
    //编写好规则
    const rules = {
      phonenum: [{
        required: true,
        message: '请输入手机号',
        trigger: 'blur'
      }, {
        pattern: /^[0-9]{11}$/,
        message: '手机号须为11位数字',
        trigger: 'blur'
      }],
      verifycode: [{
        required: true,
        message: '请输入验证码',
        trigger: 'blur'
      }, {
        pattern: /^[a-z0-9]{6,}$/,
        message: '验证码必须是6位以上英文字母或者数字',
        trigger: 'blur'
      }]
    };
    const labelPosition = ref('top');
    return {
      phone,
      rules,
      labelPosition
    };
  }
});